html, body {
  width: 100vw;
  min-height: 100vh;
  margin: 0;
  padding: 0;

  font-size: 2vh;
  font-family: sans-serif;
}

.center {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
      -ms-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}

.big-board {
  display: grid;
  grid-template-columns: 20vh 20vh 20vh;
  grid-template-rows: 20vh 20vh 20vh;
  grid-gap: 0.1vh;
}

.small-board {
  display: grid;
  grid-template-columns: 5vh 5vh 5vh;
  grid-template-rows: 5vh 5vh 5vh;
  grid-gap: 1vh;
}

.small-board-finished {
  display: grid;
  grid-template-columns: 17vh;
  grid-template-rows: 17vh;
}

.normal {
    background-color: #0F3057;
}

.indicator {
    background-color: #008891;
}

.box {
  color: #fff;
  border-radius: 15px;

  /* Align Content Vertically & Horizontally */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.material-icons.big-symbol.o { font-size: 8em; }
.material-icons.big-symbol.x { font-size: 9em; }
.material-icons.big-symbol.no-winner { font-size: 10em; }
.material-icons { font-size: 2em; }


.text-center {
  text-align: center;
}

.pad-bottom {
    padding-bottom: 15px;
}

.flex-center {
    /* Align Content Vertically & Horizontally */
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}


/* Animations */
.animate-x-big-symbol {
  -webkit-animation-duration: 0.75s;
          animation-duration: 0.75s;
  -webkit-animation-name: add-icon-big-x;
          animation-name: add-icon-big-x;
}

.animate-o-big-symbol {
  -webkit-animation-duration: 0.75s;
          animation-duration: 0.75s;
  -webkit-animation-name: add-icon-big-o;
          animation-name: add-icon-big-o;
}

.animate-no-winner-big-symbol {
  -webkit-animation-duration: 0.75s;
          animation-duration: 0.75s;
  -webkit-animation-name: add-icon-big-no-winner;
          animation-name: add-icon-big-no-winner;
}

.animate-o, .animate-x {
  /*color: green;*/
  -webkit-animation-duration: 0.75s;
          animation-duration: 0.75s;
  -webkit-animation-name: add-icon;
          animation-name: add-icon;
}

@-webkit-keyframes add-icon {
  0%   {font-size: 4em; }
  25%  {font-size: 1em;}
  60% {font-size: 2.5em;}
  100% {font-size: 2em;}
}

@keyframes add-icon {
  0%   {font-size: 4em; }
  25%  {font-size: 1em;}
  60% {font-size: 2.5em;}
  100% {font-size: 2em;}
}

@-webkit-keyframes add-icon-big-x {
  0% {
    font-size: 18em;
  }
  25% {
    font-size: 5em;
  }
  60% {
    font-size: 12em;
  }
  100% {
    font-size: 9em;
  }
}

@keyframes add-icon-big-x {
  0% {
    font-size: 18em;
  }
  25% {
    font-size: 5em;
  }
  60% {
    font-size: 12em;
  }
  100% {
    font-size: 9em;
  }
}

@-webkit-keyframes add-icon-big-o {
  0% {
    font-size: 18em;
  }
  25% {
    font-size: 5em;
  }
  60% {
    font-size: 12em;
  }
  100% {
    font-size: 8em;
  }
}

@keyframes add-icon-big-o {
  0% {
    font-size: 18em;
  }
  25% {
    font-size: 5em;
  }
  60% {
    font-size: 12em;
  }
  100% {
    font-size: 8em;
  }
}

@-webkit-keyframes add-icon-big-no-winner {
  0% {
    font-size: 18em;
  }
  25% {
    font-size: 5em;
  }
  60% {
    font-size: 12em;
  }
  100% {
    font-size: 10em;
  }
}

@keyframes add-icon-big-no-winner {
  0% {
    font-size: 18em;
  }
  25% {
    font-size: 5em;
  }
  60% {
    font-size: 12em;
  }
  100% {
    font-size: 10em;
  }
}


